import React, { useState } from "react";
import { Link } from "react-router-dom";
import { formatterCurrency } from "../../../utils/string";
import { connect, useDispatch, useSelector } from "react-redux";
import { getVariation } from "../../../api/products";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import MediaImage from "./MediaImage";
import { useTranslation } from "react-i18next";
import addToCartCheck from "./addToCartCheck";
import { toast } from "react-toastify";
import Loader from "../loaders/loader";
import InputChangerCountAddCart from "../../common/InputChangerCountAddCart";
import market from "../../../config/market";

function ProductCardRowTwo(props) {
	const dispatch = useDispatch();

	const { t } = useTranslation();

	const { cart } = useSelector((state) => state.cartlist);

	const { product, onAddToCart } = props;

	const { token } = useSelector((state) => state.user.tokenData);

	const [loading, setLoading] = useState(false);

	const { productList } = useSelector((state) => state.choosenProducts);

	const haveChoosen = productList.find(
		(p) => p.stock.id === product.stock.id
	);

	const haveCart = cart.find((p) => p.data.stock.id === product.stock.id);

	const addToCartHandler = () => {
		setLoading(true);
		getVariation(product.variation.id)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};
	const max_count = parseInt(product.variation.count);

	function onChange(qty) {
		if (parseInt(qty) > max_count) {
			toast.error(t("amount-balance"), {
				position: "bottom-left",
			});
		}
	}

	let image = product?.variation?.images[0];

	function toTop() {
		window.scroll({
			top: 0,
		});
	}
	return product ? (
		<div className="card-row-two">
			<div className="product-header">
				<Link to={`/product/${product.variation.id}`} onClick={toTop}>
					<MediaImage
						image={image}
						largeSize={"300x_"}
						mediumSize={"150x_"}
						smallSize={"150x_"}
						alt={product.name}
					/>
				</Link>
				{product.discount > 0 ? (
					<div className="discount">
						<span className="text">
							-
							{(
								100 -
								(product.newPrice * 100) / product.oldPrice
							).toFixed(0)}
							%
						</span>
					</div>
				) : null}
			</div>
			<div className="body-product">
				<h6 className="title-product">{product.variation.name}</h6>
				<div className="price-product">
					<span
						className={`price-new ${
							product.discount ? `discount` : ``
						}`}
					>
						{formatterCurrency(product.newPrice)}
					</span>
					{product.discount ? (
						<span className="price-old">
							<del>{formatterCurrency(product.oldPrice)}</del>
						</span>
					) : null}
				</div>
				<div className="add-cart-favourites">
					<div className="action-product">
						{loading ? (
							<button className="cart-btn">
								<Loader />
							</button>
						) : haveCart ? (
							<div className="product-details-quantity">
								<InputChangerCountAddCart
									initValue={haveCart.count}
									addToCartHandler={addToCartHandler}
									onChange={(value) =>
										onChange(value, product.variation.id)
									}
									variationId={product.variation.id}
									token={token}
								/>
							</div>
						) : (
							<button
								className={`${
									haveCart ? `cart-btn added` : `cart-btn`
								}`}
								onClick={addToCartHandler}
							>
								{haveCart ? (
									<>
										<img
											src={market.config.icons.cartActive}
											alt="cart"
										/>
										{t("added-cart")}
									</>
								) : (
									<>
										<img
											src={market.config.icons.cart}
											alt="cart"
										/>
										{t("add-cart")}
									</>
								)}
							</button>
						)}
					</div>
					<button
						className="btn-heart"
						onClick={() => dispatch(setChoosenProduct(product))}
					>
						{haveChoosen ? (
							<img
								src={market.config.icons.favouritesActive}
								alt="heart"
							/>
						) : (
							<img
								src={market.config.icons.favourites}
								alt="heart"
							/>
						)}
					</button>
				</div>
			</div>
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	ProductCardRowTwo
);
