import React from "react";
import { Route, Switch } from "react-router-dom";
import TrandMarkPage from "./TrandMark";
import Notfound from "../pages/404";
import { useDispatch } from "react-redux";
import { setCategoryPath } from "../../actions/any";

export default function TrandMarkRoute() {
	const dispatch = useDispatch();
	dispatch(setCategoryPath(null));

	return (
		<Switch>
			<Route exact path="/trademark/:slug" component={TrandMarkPage} />
			<Route component={Notfound} />
		</Switch>
	);
}
