import React, { useState, useEffect, Fragment } from "react";
import { connect } from "react-redux";
import InfiniteScroll from "react-infinite-scroll-component";
import store from "../../store";
import { setStatePages } from "../../actions/any";
import ProductCard from "../../components/features/product/";
import QuickView from "../../components/features/product/QuickViewNew";
import { addToCart } from "../../actions/cart";
import ToolboxThree from "../../components/features/product/details/ToolboxThree";
import ChooseVariationModal from "../../components/features/modal/choose-variation";
import LoadMoreButton from "../../components/features/product/details/loadMoreButton";
import SimpleShimmerProducts from "../../components/features/loaders/SimpleShimmerProducts";
import { useTranslation } from "react-i18next";
import { getImageUrl } from "../../utils/oxMethods";
import market from "../../config/market";
import Pagination from "../../components/features/product/details/Pagination";
import { getTrademarkProducts } from "../../api/trademarkProducts";
import BackButton from "../../components/common/BackButton";
import { dt } from "../../i18n";
import FilterCanceler from "../../components/features/product/details/FilterCanceler";
import ProductCardRowTwo from "../../components/features/product/ProductCardRowTwo";

const MORE_LOAD_PAGE_STEP = 5;
const grid = {
	"2cols": "col-6",
	"3cols": "col-6 col-md-4 col-lg-4",
	"4cols": "col-6 col-md-4 col-lg-4 col-xl-3",
	"5cols": "col-6 col-md-4 col-lg-3 col-xl-5col",
	"12cols": "col-12"
};
const queryString = require("qs");
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
	allPages: 1
};
function TrandMarkProductList({
	property_value,
	addToCart,
	setFilterOnce,
	filterOnce,
	title,
	query,
	setFilter,
	setQuery,
	column,
	setTrademark,
	toggleSideBar
}) {
	const [cols, setCols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } }
	});
	const { t } = useTranslation();

	function toTop() {
		window.scroll({
			top: 0
		});
	}

	let mount = true;
	async function getTrademarksProducts(query, more) {
		const strQuery = queryString.stringify(query);
		getTrademarkProducts(property_value, strQuery)
			.then(page => {
				setTrademark(page.brandAttributes);
				if (!filterOnce && Object.keys(page.filter).length > 0) {
					window.localStorage.setItem(
						"trademark-filter",
						JSON.stringify(page.filter)
					);

					setFilter(
						JSON.parse(
							window.localStorage.getItem("trademark-filter")
						)
					);
					setFilterOnce(true);
				}

				window.history.replaceState(
					null,
					title,
					`/trademark/${property_value}?` + strQuery
				);
				if (mount) {
					setPages(prevPage => ({
						page: page.page,
						totalCount: page.totalCount,
						variations: more
							? [...prevPage.variations, ...page.variations]
							: page.variations,

						hasMore: page.variations.length > 0,
						allPages: Math.ceil(page.totalCount / query.size)
					}));
					setLoading(false);
				}
			})
			.catch(err => {
				setPages(prevPage => ({
					...prevPage,
					hasMore: false
				}));
				setLoading(false);
			});
	}

	function fetchMoreData(load_more = false) {
		if (pages.totalCount === pages.variations.length) {
			setPages(prevPage => ({
				...prevPage,
				more_load_time: false,
				hasMore: false
			}));
			return;
		}
		if (!load_more && pages.page % MORE_LOAD_PAGE_STEP === 0) {
			setPages(prevPage => ({
				...prevPage,
				hasMore: false,
				more_load_time: true
			}));
			return;
		}
		if (query.page <= pages.allPages) {
			query.page = parseInt(query.page) + 1;
		}
		getTrademarksProducts(query, true);
	}

	function onAddToCart(product) {
		setModal({
			open: true,
			product: product
		});
	}

	useEffect(() => {
		setLoading(true);
		getTrademarksProducts(query, false);
		toTop();
		return () => {
			mount = false;
		};
	}, [query, property_value]);

	useEffect(() => {
		if (pages.variations.length > 0) {
			let p = [];
			pages.variations.map(page =>
				p.push({
					id: page.variation.id,
					name: page.variation.name,
					image: getImageUrl(page.variation.images[0], "300x_")
				})
			);
			store.dispatch(setStatePages(p));
		}
	}, [pages.variations]);

	return (
		<>
			<div className="category-title-filter">
				<div className="category-header">
					<BackButton icon={market.config.icons.backArrow} />
					<h2 className="pl-2 category-title only-mobile">
						{dt(title)}
					</h2>
				</div>
				<div className="product-toolbox">
					<button
						className="sidebar-toggler only-mobile"
						onClick={toggleSideBar}
					>
						<img src={market.config.icons.filter} alt="filter" />
						{t("filter")}
					</button>
					<ToolboxThree
						setCols={setCols}
						setQuery={setQuery}
						cols={cols}
						pages={pages}
					/>
				</div>
			</div>
			<FilterCanceler query={query} setQuery={setQuery} />

			{loading ? (
				<SimpleShimmerProducts />
			) : (
				<div className="products mb-3">
					{pages.variations.length === 0 ? (
						<div className="row w-100 m-auto">
							<h4>{t("nothing-found")}</h4>
						</div>
					) : (
						<Fragment>
							<InfiniteScroll
								dataLength={pages.variations.length}
								next={fetchMoreData}
								hasMore={pages.hasMore}
								loader={
									<div className="w-100">
										<SimpleShimmerProducts />
									</div>
								}
								className="row"
							>
								{pages.variations.map((product, i) => (
									<div className={`${grid[cols]}`} key={i}>
										{cols === "12cols" ? (
											<ProductCardRowTwo
												product={product}
												type="sidebar"
												onAddToCart={dataProduct => {
													onAddToCart(dataProduct);
												}}
											/>
										) : (
											<ProductCard
												product={product}
												type="sidebar"
												onAddToCart={dataProduct => {
													onAddToCart(dataProduct);
												}}
											/>
										)}
									</div>
								))}
							</InfiniteScroll>

							{pages.allPages > 1 ? (
								<>
									{pages.allPages > 5 &&
									query.page !== pages.allPages ? (
										<LoadMoreButton
											pages={pages}
											fetchMoreData={fetchMoreData}
										/>
									) : null}
									<div className="page-pagination">
										<Pagination
											pageCount={pages.allPages}
											onChange={({ selected }) => {
												setQuery(prev => ({
													...prev,
													page: 1 + selected
												}));
											}}
											forcePage={query.page}
										/>
									</div>
								</>
							) : null}
							<QuickView />
							<ChooseVariationModal
								setModal={setModal}
								modal={modal}
								addToCart={addToCart}
							/>
						</Fragment>
					)}
				</div>
			)}
		</>
	);
}

export default connect(null, { addToCart })(TrandMarkProductList);
