import React, { useState, useEffect } from "react";
import Filters from "./Filters";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { Field, reduxForm } from "redux-form";
import { setFiltersData } from "../../../../actions/any";
import { useTranslation } from "react-i18next";
import market from "../../../../config/market";
function calculateChoosenPropsCount(props) {
	return (
		(props && Object.values(props).reduce((cv, pv) => cv + pv.length, 0)) ||
		0
	);
}
function FilterSideTwo(props) {
	const filter = props.filter;
	const hideSideBar = props.hideSideBar;
	const [toggle, setToggle] = useState(false);
	const { t } = useTranslation();

	let count =
		(props.initialValues &&
			calculateChoosenPropsCount(props.initialValues.productProperties) +
				calculateChoosenPropsCount(
					props.initialValues.variationProperties
				)) ||
		0;

	useEffect(() => {
		window.addEventListener("resize", resizeHandle);
		resizeHandle();

		return () => {
			window.removeEventListener("resize", resizeHandle);
		};
	}, []);
	function counter({ input, className, placeholder }) {
		return (
			<input
				{...input}
				className={className}
				type="number"
				placeholder={placeholder}
				onChange={(e) => {
					input.onChange(e.target.value);
				}}
			/>
		);
	}

	function resizeHandle() {
		if (window.document.querySelector("body")?.offsetWidth < 992)
			setToggle(true);
		else setToggle(false);
	}

	if (
		filter &&
		filter.variationProperties?.length === 0 &&
		filter.productProperties?.length === 0
	) {
		return null;
	}

	if (!filter) {
		return <ShimmerProduct />;
	}
	const { handleSubmit } = props;

	return (
		<form onSubmit={handleSubmit}>
			<div className="filter-close-but">
				{market.config.home === 5 ? (
					<button
						// disabled={count < 1 ? true : false}
						className="btn-filter-close"
						onClick={() => hideSideBar()}
					>
						<span className="filter-closer">{t("close")}</span>
					</button>
				) : (
					<div
						className="btn-filter-close"
						onClick={() => hideSideBar()}
					>
						<span className="filter-closer">
							{t("close-filter")}
						</span>
					</div>
				)}
			</div>
			<a
				href="##"
				className="sidebar-filter-clear d-none"
				onClick={(e) => {
					e.preventDefault();
					props.clearAll();
				}}
			>
				{t("clear-all")}
			</a>
			<aside
				className={toggle ? "sidebar-filter" : "sidebar sidebar-shop"}
			>
				<div className="filter-header d-none">
					<button className="closer" onClick={() => hideSideBar()}>
						<img src={market.config.icons.backArrow} alt="back" />
					</button>
					<p>{t("filters")}</p>
				</div>
				<div className={toggle ? "sidebar-filter-wrapper" : ""}>
					<div className="widget widget-clean not-top">
						<label>
							{market.config.home === 5
								? `Выбрано: ${count}`
								: t("filter")}
						</label>
						<a
							href="##"
							className="sidebar-filter-clear"
							onClick={(e) => {
								e.preventDefault();
								props.clearAll();
							}}
						>
							{market.config.home === 5 && count > 0
								? t("reset")
								: t("clear-all")}
						</a>
						<div className="filter-close-but d-none">
							<div
								className="btn-filter-close"
								onClick={() => hideSideBar()}
							>
								<i className="icon-close"></i>
								<span>{t("close-filter")}</span>
							</div>
						</div>
					</div>
					{/* <div className="filter-minCount">
						<h4>{t("min_count")}</h4>
						<div className="d-flex justify-content-between">
							<Field
								name="sellPrice"
								type="number"
								className="form-control mb-0 mr-4"
								// placeholder={t("from-two")}
								component={counter}
							/>
							<button className="btn ml-4 p-0">Применить</button>
						</div>
					</div> */}

					<div className="filter-price mb-2">
						<h4>{t("price-from-to")}</h4>
						<div className="d-flex justify-content-between">
							<Field
								name="sellPrice[min]"
								type="number"
								className="form-control mb-0 mr-4"
								placeholder={t("from-two")}
								component={counter}
							/>
							<Field
								name="sellPrice[max]"
								type="number"
								className="form-control mb-0"
								placeholder={t("to")}
								component={counter}
							/>
							<button className="btn ml-4 p-0">
								{t("accepted")}
							</button>
						</div>
					</div>
					{filter.variationProperties &&
					filter.variationProperties !== [] ? (
						<Filters
							filters={[
								"variationProperties",
								Object.entries(filter.variationProperties),
							]}
						/>
					) : null}
					{filter.productProperties &&
					filter.productProperties !== [] ? (
						<Filters
							filters={[
								"productProperties",
								Object.entries(filter.productProperties),
							]}
						/>
					) : null}
				</div>
				{market.config.home === 4 ? (
					<div className="widget widget-clean">
						<label>{t("filter")}:</label>
						<a
							href="##"
							className="sidebar-filter-clear"
							onClick={(e) => {
								e.preventDefault();
								props.clearAll();
							}}
						>
							{t("clear-all")}
						</a>
					</div>
				) : null}
			</aside>
			<div className="sidebar-filter-overlay" onClick={hideSideBar}></div>
		</form>
	);
}

export default reduxForm(
	{
		form: "filterSupplierFormList",
		onChange: (values, dispatch, props, previousValues) => {
			props.submit();
		},
		enableReinitialize: true,
	},
	{ setFiltersData }
)(FilterSideTwo);
