import React, { useRef, useState } from "react";
import { deepEqual } from "../../utils/array";
import Company from "../supplier/supplier-header/info-company";
import TrandMarkProductList from "./TrandMarkProductList";
import FilterSideTwo from "../category/filterSidebar/filters/FilterSideTwo";
import { useParams } from "react-router-dom";

const queryString = require("qs");
let timeOut = setTimeout(() => {}, 1000);
const KEEP_TIME = 1000;
const initialQuery = { page: 1, size: 20 };

function TrandMarkOne() {
	const [trademark, setTrademark] = useState();
	const [filterOnce, setFilterOnce] = useState(false);
	const { slug: property_value } = useParams();
	const [filter, setFilter] = useState(null);
	const [query, setQuery] = useState(
		window.location.search.length > 0
			? queryString.parse(window.location.search.slice(1))
			: initialQuery
	);

	const myRef = useRef(null);

	function hideSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
	}

	function toggleSideBar() {
		if (
			document
				.querySelector("body")
				.classList.contains("sidebar-filter-active")
		)
			document
				.querySelector("body")
				.classList.remove("sidebar-filter-active");
		else
			document
				.querySelector("body")
				.classList.add("sidebar-filter-active");
	}
	return (
		<>
			<div className="trademark-page" ref={myRef}>
				<div className="page-content">
					<div className="container">
						<div className="row">
							<div className="col-lg-3 skeleton-body skel-shop-sidebar loaded">
								{trademark && (
									<Company supplier={trademark[0]} />
								)}
								{filter ? (
									<FilterSideTwo
										onSubmit={values => {
											clearTimeout(timeOut);
											timeOut = setTimeout(() => {
												if (!deepEqual(query, values)) {
													setQuery(prevQuery => ({
														...prevQuery,
														...values,
														page: 1
													}));
												}
											}, KEEP_TIME);
										}}
										clearAll={() => {
											setQuery(initialQuery);
										}}
										filter={filter}
										adClass="sidebar sidebar-shop"
										initialValues={query}
										hideSideBar={hideSideBar}
									/>
								) : null}

								<div
									className="sidebar-filter-overlay"
									onClick={hideSideBar}
								></div>
							</div>

							<div className="col-lg-9 skeleton-body skel-shop-products loaded">
								<TrandMarkProductList
									setFilter={setFilter}
									setTrademark={setTrademark}
									setFilterOnce={setFilterOnce}
									filterOnce={filterOnce}
									toggleSideBar={toggleSideBar}
									query={query}
									title={property_value}
									column="4cols"
									setQuery={setQuery}
									property_value={property_value}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

export default TrandMarkOne;
