import React from "react";
import { useTranslation } from "react-i18next";
import { connect } from "react-redux";
import FilterCancelButton from "./FilterCancelButton";

function filterChooser(query, filtersData) {
	let choosedFilters = [];
	// suppliers;
	if (query.properties) {
		for (const prop in query.properties) {
			const queryProp = query.properties[prop]; //ARRAY
			const filtersDataProp = filtersData.variationProperties[prop]; // OBJECT {choices:[], label:""}
			const propName = filtersDataProp?.label;

			for (const choice of queryProp) {
				const cancelState = JSON.parse(JSON.stringify(query));

				cancelState.properties[prop] = cancelState.properties[
					prop
				].filter(prop => prop !== choice);
				choosedFilters.push({
					name: propName,
					value: choice,
					cancelState
				});
			}
		}
	}
	if (query.productProperties) {
		for (const prop in query.productProperties) {
			const queryProp = query.productProperties[prop]; //ARRAY
			const filtersDataProp = filtersData.productProperties[prop]; // OBJECT {choices:[], label:""}
			const propName = filtersDataProp.label;

			for (const choice of queryProp) {
				const cancelState = JSON.parse(JSON.stringify(query));

				cancelState.productProperties[
					prop
				] = cancelState.productProperties[prop].filter(
					prop => prop !== choice
				);
				choosedFilters.push({
					name: propName,
					value: choice,
					cancelState
				});
			}
		}
	}

	return choosedFilters;
}

function FilterCanceler({ query, filtersData, setQuery }) {
	const { t } = useTranslation();
	let empty = false;
	if (!query.properties && !query.productProperties && !query.suppliers) {
		empty = true;
	}
	let choosedFilters = empty ? [] : filterChooser(query, filtersData);

	if (empty || !choosedFilters.length) {
		return null;
	}
	return (
		<div className="toolbox" style={empty ? { display: "none" } : {}}>
			<div className="toolbox-center">
				<div className="toolbox-info filter-cancelers">
					{choosedFilters.map((choice, index) => (
						<FilterCancelButton
							key={index}
							setQuery={setQuery}
							item={choice}
						/>
					))}
					<div className="canceler-button clear-all">
						<button
							onClick={e => {
								e.preventDefault();
								setQuery({ page: 1, size: 12 });
							}}
						>
							<span>{t("clear-all")}</span>
						</button>
					</div>
				</div>
			</div>
		</div>
	);
}

export default connect(state => ({ filtersData: state.any.filtersData }))(
	FilterCanceler
);
